* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

.App {
	text-align: center;
}

nav {
	background-color: rgb(12, 20, 92);
	color: white;
	height: 10vh;
	width: 100vw;
	padding: 10px;
	font-size: larger;
	line-height: 8vh;
}
.main {
	height: 90vh;
	width: 100vw;
	background-color: rgba(0, 0, 0, 0.8);
	display: flex;

	justify-content: center;
	align-items: center;
}
.main .form {
	height: 80vh;
	width: 40vw;
	display: flex;
	justify-content: space-between;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.main .form .sub {
	width: 100%;
	height: 5vh;
	color: white;
	font-size: x-large;
}

.main .form .sub label {
	width: 40%;
}
.main .form .sub input {
	width: 40%;
}
@media only screen and (min-width: 120px) {
	.main .form {
		width: 100vw;
	}
	.main .form .sub {
		font-size: smaller;
	}
}

@media only screen and (min-width: 320px) {
	.main .form {
		width: 100vw;
	}
	.main .form .sub {
		font-size: medium;
	}
}
@media only screen and (min-width: 420px) {
	.main .form {
		width: 80vw;
	}
	.main .form .sub {
		font-size: x-large;
		height: 7vh;
	}
}
@media only screen and (min-width: 720px) {
	.main .form {
		width: 60vw;
	}
	.main .form .sub {
		font-size: x-large;
		height: 7vh;
	}
}
